import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
import { OcppChargerComponent } from './ocpp-charger/ocpp-charger.component';
const routes : Routes = [
    {
        path : 'ocppDashboard',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppChangeAvailability',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppManageConfigurations',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppGetDiagnostic',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppRemoteStart',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppRemoteStop',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppUnlockConnector',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppUpdateFirmware',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppReserveNow',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppCancelReservation',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppDataTransfer',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppSendLocalList',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppTriggerMessage',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppGetCompositeSchedule',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppClearChargingProfile',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
    {
        path : 'ocppSetChargingProfile',component : OcppChargerComponent,canActivate: [AuthGuard]
    },
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class OccpChargerProfileRoutingModule{}